import Link from "next/link";
import "../css/components/workshopCard.css";
import type { Workshop, Verein } from "@prisma/client";

export default function WorkshopCard({
  workshop,
}: {
  workshop: Workshop & { Verein?: Verein };
}) {
  const instagramUrl =
    workshop.instagramPostUrl || workshop.Verein?.instagramProfileUrl || "#"; // Fallback auf Instagram-Profil oder Standard-URL

  return (
    <div className="workshop-card">
      <Link href={instagramUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={`/${workshop.imageUrl}`} // Pfad relativ zum public-Ordner
          alt={workshop.title}
          className="workshop-image"
        />
      </Link>
      <div className="workshop-details">
        <Link href={instagramUrl} target="_blank" rel="noopener noreferrer">
          <div className="workshop-title-container">
            <img
              src={`/${workshop.Verein?.avatarUrl || "img/default-avatar.png"}`} // Standardpfad verwenden, falls avatarUrl null ist
              alt={workshop.Verein?.name || "Default Name"}
              className="workshop-avatar"
            />
            <h2 className="workshop-title">{workshop.title}</h2>
          </div>
        </Link>
        <div className="workshop-info">
          <p className="date">{new Date(workshop.date).toLocaleDateString()}</p>
          <p className="location">{workshop.location}</p>
          <p className="price">{workshop.cost}€</p>
        </div>
      </div>
    </div>
  );
}
